import React, { Component, Suspense } from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import SVG from 'react-inlinesvg';
import $ from 'jquery'
import { Modal, Carousel } from 'react-bootstrap';
import ProfileImage from '../../CommonComponents/ProfileImage/ProfileImage';
import Media from '../../CommonComponents/Media/Media';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../Services/Utils/utils';
const GetRightsModalAside = React.lazy(() => import('./Elements/GetRightsModalAside'));
window.$ = $;
global.jQuery = $
class GetRightsModal extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        index: 0,
        direction: null,
    }
    mainRef = React.createRef();
    thumbsRef = React.createRef();
    componentDidMount() {
        if (this.mainRef.current && this.thumbsRef.current && this.thumbsRef.current.splide) {
            this.mainRef.current.sync(this.thumbsRef.current.splide);
        }
    }

    handleSelect = (selectedIndex, e) => {
        if (e) this.setState({
            index: selectedIndex,
            direction: e.direction
        });
    }
    handleClose = (event) => {
        this.props.rightsModalCallback(false)
    };
    carousalImages = (bulkRightsData, thumb) => {
        return bulkRightsData.map((grItem, grindx) => <SplideSlide key={grindx}>
            <Media mediaType={thumb ? grItem.type : 2} postFile={grItem.postFile} mediaHeight={grItem.mediaHeight} mediaWidth={grItem.mediaWidth} socialNetwork={''} textContent={grItem.content} rating={grItem.rating} alt={grItem.name} />
            {thumb ? <div className="rights_carousel_caption position-relative">
                <div className="d-flex align-items-center justify-content-between p-2">
                    <a href={grItem.link} target={'_blank'} className="text-decoration-none d-flex align-items-center flex-grow-1 mr-2 w-75 me-3">
                        <ProfileImage className={'symbol symbol-50 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                            imageSize={50}
                            symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-4'}
                            name={''} profileUrl={grItem.picture} networkId={grItem.networkId} />
                        <div className="d-flex flex-column w-100 overflow-hidden">
                            <p className="fs-7 text-dark mb-0 fw-semibold text-ellipsis">{grItem.name}</p>
                            <span className="mb-0 text-muted text-ellipsis">@{grItem.username}</span>
                        </div>
                    </a>
                    <div className="d-flex flex-shrink-0">
                        <span className="h-30px w-30px">
                            <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(grItem.networkId)}.svg`)} alt={GET_NETWORK_NAME(grItem.networkId)} height={30} width={30} />
                        </span>
                    </div>
                </div>
            </div> : null}
        </SplideSlide>)
    };
    render() {
        //const {show} = this.state
        const { rightsModalToggle, modalAssetData, networkData, bulkRightsData } = this.props
        const isBulkStatus = bulkRightsData && bulkRightsData.length > 0 ? true : false


        const mainOptions = {
            type: 'fade',
            heightRatio: 'auto',
            pagination: false,
            arrows: true,
            cover: true,
        };

        const thumbsOptions = {
            rewind: true,
            arrows: false,
            fixedWidth: 80,
            fixedHeight: 80,
            isNavigation: true,
            gap: 5,
            focus: 'center',
            pagination: false,
            cover: true,
            dragMinThreshold: {
                mouse: 4,
                touch: 10,
            },
            breakpoints: {
                640: {
                    fixedWidth: 60,
                    fixedHeight: 60,
                },
            },
        };

        return <Modal size={'full'} show={rightsModalToggle} className="rights_modal border-0 p-0 bg-white" backdrop={false}>
            <Modal.Header className="border-0 p-0 w-100">
                <div className="d-flex justify-content-between align-items-center px-lg-3 px-2 h-46px border-bottom bg-white w-100">
                    <div className="d-flex align-items-center flex-wrap">
                        <h1 className="text-dark font-weight-bold mr-4 fs-6 mb-0">
                                
                            {modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 2 ?
                                <span className="svg-icon svg-icon-2 text-success">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-approved-fill.svg')} height={20} width={20} />
                                </span>:
                                modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 1 ?
                                <span className="svg-icon svg-icon-2 text-warning">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-pending-fill.svg')} height={20} width={20} />
                                </span> : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 3 ?
                                <span className="svg-icon svg-icon-2 text-danger">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-rejected-fill.svg')} height={20} width={20} />
                                </span> :
                                <span className="svg-icon svg-icon-2 text-success">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-approved-fill.svg')} height={20} width={20} />
                                </span>
                            }
                            Get Rights
                        </h1>
                    </div>
                    <button type="button" className="btn-close me-2 ms-0 text-light" onClick={this.handleClose}></button>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex align-items-stretch border-0 flex-wrap">
                <div className="rights_modal_inner d-flex flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-2 mb-lg-0">
                    <div className="rights_modal_img_wrapper pt-3 w-100">

                        {isBulkStatus ?
                            <div className="rounded-1 overflow-hidden">
                                <Splide className="mb-3 rights_thumb" options={thumbsOptions} ref={this.thumbsRef}>
                                    {this.carousalImages(bulkRightsData, false)}
                                </Splide>
                                <Splide options={mainOptions} ref={this.mainRef}>
                                    {this.carousalImages(bulkRightsData, true)}
                                </Splide>
                            </div>
                            :
                            <div className="right_slide_img rounded-1 overflow-hidden">
                                <Media mediaType={modalAssetData.type} postFile={modalAssetData.postFile} mediaHeight={modalAssetData.mediaHeight} mediaWidth={modalAssetData.mediaWidth} socialNetwork={''} textContent={modalAssetData.content} rating={modalAssetData.rating} alt={modalAssetData.name} />
                                <div className="rights_carousel_caption position-relative">
                                    <div className="d-flex align-items-center justify-content-between p-2">
                                        <a href={modalAssetData.link} target={'_blank'} className="text-decoration-none d-flex align-items-center flex-grow-1 mr-2 w-75 me-3">
                                            <ProfileImage className={'symbol symbol-50 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                imageSize={50}
                                                symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-4'}
                                                name={''} profileUrl={modalAssetData.picture} networkId={modalAssetData.networkId} />
                                            <div className="d-flex flex-column w-100 overflow-hidden">
                                                <p className="fs-7 text-dark mb-0 fw-semibold text-ellipsis">{modalAssetData.name}</p>
                                                <span className="mb-0 text-muted text-ellipsis">@{modalAssetData.username}</span>
                                            </div>
                                        </a>
                                        <div className="d-flex flex-shrink-0">
                                            <span className="h-30px w-30px">
                                                <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`)} alt={GET_NETWORK_NAME(modalAssetData.networkId)} height={30} width={30} />
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        }
                    </div>
                </div>
                <Suspense fallback={<></>}>
                    <GetRightsModalAside modalClose={this.handleClose} modalAssetData={modalAssetData} isBulkStatus={isBulkStatus} bulkRightsData={bulkRightsData} onUpdateRejectedRightDetail={this.props.onUpdateRejectedRightDetail} />
                </Suspense>
            </Modal.Body>
        </Modal >
    }
}

export default GetRightsModal;